import {useState} from 'react' 

const Searchbar = () => {
    const UniList = {
        'NaUKMA НаУКМА Національний університет «Києво-Могилянська академія»':'Національний університет «Києво-Могилянська академія»',
        'УКУ UCU Український Католицький Університет': 'Український Католицький Університет'
    }
    const [propositions, setPropositions] = useState([])
    const [searchbar, setSearchbar] = useState('')
    function onSearchBarChange(target){
        setSearchbar(target.value)
        let Propositions = []
        let temp = target.value.split(" ")
        for(let part of temp){
            part = part.toLowerCase()
            for(let key of Object.keys(UniList)){
                if (key.toLowerCase().includes(part) && !Propositions.includes(UniList[key]) && part!==''){
                    Propositions.push(UniList[key])
                }
            }
        }
        setPropositions(Propositions)
    }
    function handleEnter(e){
        if(e.key === 13){
            e.preventDefault()
            return false
        } 
    }
    return (
        <section class="forms">
              <div class="container-fluid" style={{'width':'70%', 'margin':'0px auto'}}>
                <div class="row" >
                    <div class="col-md-12">
                        <div class="section-heading" style={{'float':'left'}}>
                            <h2>Search</h2>
                        </div>
                    </div>
                    <form method="post" id='contact'>
                        <div className='col-md-6' style={{'width':'100%', 'margin':'0px auto'}}>
                            <fieldset className='center'>
                                <input id="name" type="text" placeholder='Type in some letters...' onChange={(e) => onSearchBarChange(e.target)} onKeyPress={e => handleEnter(e)} value={searchbar} class='form-control'/>
                            </fieldset>
                        </div>
                        {
                            propositions.map(value => <input type="submit" value={value} onClick={(e)=>setSearchbar(e.target.value)}/>)
                        }
                    </form>
                </div>
            </div>
        </section>
    )
}

export default Searchbar
