import React from 'react'
import Lecturer from './Lecturer'
import Searchbar from './Searchbar'

const Lecturers = ({data}) => {
    //imitated_data  = data

    
    const imitated_data = [{
        'src': 'http://cs.ucu.edu.ua/wp-content/themes/twentythirteen/images/ucu_logo_uk.png',
        'title': 'Ukrainian Catholic University',
        'stats_srcs':['/media/1','/media/1','/media/1']
        },
        {
        'src':'https://nucc.no/wp-content/uploads/2016/12/logo-NaUKMA.png',
        'title': 'Ukrainian Catholic University',
        'stats_srcs':['/media/1','/media/1','/media/1']
        }
    ]


    return (
        <div>
        <div className='universities'>
            <Searchbar />
            {
                imitated_data.map( ({src, title, stats_srcs}) => <Lecturer src={src} title={title} stats_srcs={stats_srcs}/>)
            }
        </div>
        </div>
    )
}

export default Lecturers
