const Login = ({data}) => {
        return (
        <section className='contact-form' style={{'margin':'100px'}}>
            <div className="row specially-for-form">
                <div className="col-md-6">
                    <form id="contact" method="post">
                            <div className="col-md-12">
                                <fieldset>
                                    <input name='email' type="email" id="email" size="30" placeholder='Email' required />
                                </fieldset>
                            </div>
                            <div className="col-md-12">
                                <fieldset>
                                    <input  name="password" type="password" placeholder='Password...' required/>
                                </fieldset>
                            </div>
                            <div className="col-md-12" >
                                <fieldset>
                                <button type="submit" id="form-submit" class="button">Login</button> 
                                </fieldset>
                            </div>
                            <div className="col-md-12" style={{'margin-top':'20px'}}>
                                <div className="border-rounded-button" style={{'width':'20%','margin':'0px auto'}}>
                                    <a href="/registration" style={{'text-decoration':'none'}}>Register</a>
                                </div>
                            </div> 
                    </form>
                </div>
            </div>
        </section>
        )
}

export default Login
