import React from 'react'

const Profile = ({data}) => {
    const name = 'Dima'
    const surname = 'Kondratenko'
    const university = 'Kyiv-Mohyla Academy'
    const faculty = 'Faculty of Informatics'
    const specialty = '122 Computer Science'
    return (
        <div className='profile'>
            <h2><span>Name</span>: <strong>{name}</strong></h2>
            <h2><span>Surname</span>: <strong>{surname}</strong></h2>
            <h2><span>University</span>: <strong>{university}</strong></h2>
            <h2><span>Faculty</span>: <strong>{faculty}</strong></h2>
            <h2><span>Specialty</span>: <strong>{specialty}</strong></h2>
            <div className='col-md-6 col-sm-12'>
                <div className='row'>
                    <div className="col-md-6">
                        <div className="border-rounded-button">
                            <a href="/logout">Log Out</a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Profile
