import React from 'react'
import {useState} from 'react'

const Registration = ({data}) => {
    const [Abit, setAbit] = useState('Student')
    return (
    <section className='contact-form'>
        <div className="row specially-for-form">
            <div className="col-md-6">
                <form id="contact" method="post">
                        <div class="col-md-4 col-sm-4" style={{"margin":"10px 0px"}}>
                          <div class="circle-item">
                            <input name="Status" 
                                   id="demo-small"
                                   type="radio" 
                                   value='Student' 
                                   onClick={(e)=>{console.log(1); setAbit(e.target.value); console.log(e.target.value); e.target.checked=!e.target.checked}} 
                                   checked={'Student' === Abit} />
                            <label for="demo-small">Студент</label>
                          </div>
                        </div>
                        <div class="col-md-4 col-sm-4" style={{"margin":"10px 0px"}}>
                          <div class="circle-item">
                            <input name="Status" 
                                   id="demo-medium"
                                   type="radio" 
                                   value="Enrollee" 
                                   onClick={(e)=>{console.log(2); setAbit(e.target.value); console.log(e.target.value); e.target.checked=!e.target.checked}} 
                                   checked={'Enrollee' === Abit} />
                            <label for="demo-medium">Абітуріент</label>
                          </div>
                        </div>

                        <div className="col-md-12">
                            <fieldset>
                            <input type="text" name="name" placeholder='Name...' required/>
                            </fieldset>
                        </div>
                        <div className="col-md-12">
                            <fieldset>
                            <input type="text" name="surname" placeholder='Surname...' required/>
                            </fieldset>
                        </div>
                        <div className="col-md-12">
                            <fieldset>
                            <input type="password" name="password" placeholder='Password...' required/>
                            </fieldset>
                        </div>
                        { Abit==='Student' &&
                            <div className="col-md-12">
                                <fieldset>
                                <input type="email" id="email" size="30" placeholder='University email' required />
                                </fieldset>
                            </div>
                        }
                        { Abit==='Enrollee' &&
                            <div className="col-md-12">
                                <fieldset>
                                <input type="email" id="email" size="30" placeholder='Email' required />
                                </fieldset>
                            </div>
                        }
                        <div className="col-md-12">
                            <fieldset>
                            <button type="submit" id="form-submit" class="button">Register</button>
                            </fieldset>
                        </div>
                        <div className="col-md-12" style={{'margin-top':'20px'}}>
                            <div className="border-rounded-button" style={{'width':'20%','margin':'0px auto'}}>
                                <a href="/login" style={{'text-decoration':'none'}}>Login</a>
                            </div>
                        </div> 
                </form>
            </div>
        </div>
    </section>
    )
}

export default Registration
