import {useState} from 'react'

const Navbar = () => {
    const  [class_name,setclassname]  = useState('inactive')
    const  [style,setstyle] = useState({
            'left': '0px',
            'top': '0px'
    })


    function eventHandler(){
        if(class_name==='inactive'){
            setclassname('')
            setstyle({
                'left': '400px',
                'font-size':'70px'
            })
        }else{
            setclassname('inactive')
            setstyle({
                'left': '0px',
                'font-size':'50px'
            })
        }
    }
    
    return (
    <div>
        <p className='toggle' style={style} onClick={(e) => eventHandler()}>{class_name==='inactive' ? '☰' : '×'}</p>
        <div id="sidebar" className={class_name}>
        <div class="inner">
        <section id="search" class="alt">
        </section>
        <nav id="menu">
            <ul>
                <li><a href="/">Homepage</a></li>
                <li><a href="/universities">Universities Stats</a></li>
                <li><a href="/lecturers">Lecturers Stats</a></li>
                <li><a href="/login">Login or Register</a></li> 
                {
                    // Should check if person registeres, if person logged in
                }
                <li><a href="/profile">Profile</a></li>
            </ul>
        </nav>
        <footer id="footer">
              <p class="copyright">
              <br />Designed by <a rel="nofollow" href="https://www.facebook.com/templatemo">Template Mo</a></p>
        </footer>
        </div>
        </div>
    </div>
    )
}

export default Navbar
